export class StavesService {
  #osmd;
  #tracks;
  #from;
  #to;
  #xmlUrl;

  #currentVoiceEntryIndex; // for differentiating between checks (tie start/end cases), it's not the same as steps
  #currentStep; // the position (from 0) of current note among all separate notes
  #separateNoteService;

  constructor({ osmd, tracks, from, to, xmlUrl, separateNoteService }) {
    this._validateParams({ osmd, tracks, from, to, xmlUrl, separateNoteService });

    this.#osmd = osmd;
    this.#xmlUrl = xmlUrl;
    this.#from = from;
    this.#to = to;
    this.#tracks = tracks;

    this.#separateNoteService = separateNoteService;
    this.#currentVoiceEntryIndex = 0;
    this.#currentStep = 0;
  }

  async start() {
    await this._loadSong();

    if (this.#from === 0 && this._hasSeparateNoteOnAnyActiveTrack()) {
      return;
    }

    this.next();
  }

  next() {
    this.#currentVoiceEntryIndex += 1;
    const endReached = this._moveCursor();

    const isOutOfLimits = endReached || (this.#to !== null && this.#currentStep >= this.#to);
    if (isOutOfLimits) {
      return;
    }

    if (this._hasSeparateNoteOnAnyTrack()) {
      this.#currentStep += 1;
    }

    if (this.#currentStep >= this.#from && this._hasSeparateNoteOnAnyActiveTrack()) {
      return;
    }

    return this.next();
  }

  getCurrentStep() {
    return this.#currentStep;
  }

  _validateParams(params) {
    const validator = {
      osmd: (val) => typeof val === 'object',
      tracks: (val) => ['first', 'second', 'both'].includes(val),
      from: (val) => typeof val === 'number' && val >= 0,
      to: (val) => (typeof val === 'number' && val >= 0) || val === null,
      xmlUrl: (val) => typeof val === 'string',
      separateNoteService: (val) => typeof val === 'object',
    };

    Object.entries(params).forEach(([key, value]) => {
      const validate = validator[key];

      if (!validator) {
        throw Error(`Unknown parameter ${key}`);
      }

      if (!validate(value)) {
        throw Error(`Invalid parameter ${key}. Received ${value}`);
      }
    });
  }

  async _loadSong() {
    await this.#osmd.load(this.#xmlUrl);
    this.#osmd.render();
    this.#osmd.cursor.show();
  }

  _getCurrentVoiceEntryForTrack(trackIndex) {
    return this.#osmd?.cursor?.iterator?.currentVoiceEntries?.[trackIndex];
  }

  _hasSeparateNoteOnAnyActiveTrack() {
    if (['first', 'second'].includes(this.#tracks)) {
      const trackIndex = this.#tracks === 'first' ? 0 : 1;
      const voiceEntry = this._getCurrentVoiceEntryForTrack(trackIndex);

      return this.#separateNoteService.isSeparateNote(
        voiceEntry,
        this.#currentVoiceEntryIndex,
        trackIndex
      );
    }

    return this._hasSeparateNoteOnAnyTrack();
  }

  _hasSeparateNoteOnAnyTrack() {
    const voiceEntryA = this._getCurrentVoiceEntryForTrack(0);
    const voiceEntryB = this._getCurrentVoiceEntryForTrack(1);
    const hasSeparateNoteOnTrackA = this.#separateNoteService.isSeparateNote(
      voiceEntryA,
      this.#currentVoiceEntryIndex,
      0
    );
    const hasSeparateNoteOnTrackB = this.#separateNoteService.isSeparateNote(
      voiceEntryB,
      this.#currentVoiceEntryIndex,
      1
    );

    return hasSeparateNoteOnTrackA || hasSeparateNoteOnTrackB;
  }

  _moveCursor() {
    if (this.#osmd.cursor.iterator.endReached) {
      return true;
    }

    this.#osmd.cursor.next();
  }
}
