<template>
  <form @submit.prevent="onSubmit" class="form">
    <b>{{ currentStep }}</b>

    <label for="leftTrack">левая рука</label>
    <input id="leftTrack" type="checkbox" name="leftTrack" v-model="leftTrack" />

    <label class="form__item" for="rightTrack">правая рука</label>
    <input id="rightTrack" type="checkbox" name="rightTrack" v-model="rightTrack" />

    <button
      class="form__btn"
      :disabled="isSelectingSlice"
      type="submit"
      @click.prevent="isSelectingSlice = true"
    >
      выбрать отрезок
    </button>
    <button class="form__btn" :disabled="!isUpdated" type="submit">подтвердить</button>
  </form>
</template>

<script>
import { songControlsStore, setSongControlsStoreValue } from '@/store/songControls';
import { dispatchRestart } from '@/store/restartAction';

export default {
  name: 'SongControls',
  tracksOptions: ['first', 'second', 'both'],
  data() {
    return {
      leftTrack: true,
      rightTrack: true,
      isUpdated: false,
    };
  },
  computed: {
    currentStep() {
      return songControlsStore.currentStep;
    },
    tracks() {
      if (this.leftTrack && this.rightTrack) {
        return 'both';
      }

      return this.rightTrack ? 'first' : 'second';
    },
    from() {
      return songControlsStore.from;
    },
    to() {
      return songControlsStore.to;
    },
    isSelectingSlice: {
      get() {
        return songControlsStore.isSelectingSlice;
      },
      set(value) {
        setSongControlsStoreValue('isSelectingSlice', value);
      },
    },
  },
  watch: {
    from: 'markAsUpdated',
    to: 'markAsUpdated',
    tracks: 'markAsUpdated',
  },
  methods: {
    markAsUpdated() {
      this.isUpdated = true;
    },
    markAsSaved() {
      this.isUpdated = false;
    },
    onSubmit() {
      setSongControlsStoreValue('tracks', this.tracks);
      this.isSelectingSlice = false;
      dispatchRestart();
      this.markAsSaved();
    },
  },
};
</script>

<style scoped>
.form {
  padding: 24px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 8px;
  height: 50px;
}

.form__btn {
  height: 100%;
  font-size: inherit;
}
</style>
