import lodash from 'lodash';

const areEqualSets = (eventsSetA, eventsSetB) => {
  if (eventsSetA.length !== eventsSetB.length) {
    return false;
  }

  const setsA = lodash.groupBy(eventsSetA, (e) => `${e.getEventType()}:${e.getNote()}`);
  const setsB = lodash.groupBy(eventsSetB, (e) => `${e.getEventType()}:${e.getNote()}`);

  const keysA = Object.keys(setsA);
  const keysB = Object.keys(setsB);

  if (keysA.length !== keysB.length) {
    return false;
  }

  return Object.keys(setsA).every((k) => setsA[k]?.length === setsB[k]?.length);
};

export class SimultaneousMidiEvents {
  #prevEventsDeltaTimeCumulativeShift;

  constructor(midiEvents, prevEventsDeltaTimeCumulativeShift = 0) {
    this.midiEvents = midiEvents;
    this.#prevEventsDeltaTimeCumulativeShift = prevEventsDeltaTimeCumulativeShift;
    this.eventType = 'simultaneous';
    this.voiceEntryIndex = null;
  }

  addEvent(midiEvent) {
    this.midiEvents.push(midiEvent);
  }

  filterEventsByTrackIndex(trackIndex) {
    this.midiEvents = this.midiEvents.filter((event) => event.getTrackIndex() === trackIndex);
  }

  isEmpty() {
    return this.midiEvents.length === 0;
  }

  setVoiceEntryIndex(index) {
    this.voiceEntryIndex = index;
  }

  getVoiceEntryIndex() {
    return this.voiceEntryIndex;
  }

  getDeltaTime() {
    const [firstEvent] = this.midiEvents;

    if (!firstEvent) {
      return 0;
    }

    return firstEvent.getDeltaTime() + this.#prevEventsDeltaTimeCumulativeShift;
  }

  getCumulativeDeltaTimeShift() {
    return this.midiEvents.slice(1).reduce((total, e) => e.getDeltaTime() + total, 0);
  }

  hasKeyPressEvents() {
    return this.midiEvents.some((e) => e.isKeyPressEvent());
  }

  getMidiEvents() {
    return this.midiEvents;
  }

  isEqualByKeypressEvents(simultaneousEvents) {
    const keyPressEventsA = this.getMidiEvents().filter((e) => e.isKeyPressEvent());
    const keyPressEventsB = simultaneousEvents.getMidiEvents().filter((e) => e.isKeyPressEvent());

    return areEqualSets(keyPressEventsA, keyPressEventsB);
  }

  getSize(keyPressOnly) {
    if (keyPressOnly) {
      return this.midiEvents.filter((e) => e.isKeyPressEvent()).length;
    }

    return this.midiEvents.length;
  }
}
