import { SimultaneousMidiEvents } from '@/models/SimultaneousMidiEvents';

export class SimultaneousMidiEventsComparator {
  #threshold;
  #testedMidiEvents;
  #lastCheckTimestamp;

  constructor(threshold) {
    this.#threshold = threshold;
    this.#testedMidiEvents = new SimultaneousMidiEvents([]);
    this.#lastCheckTimestamp = 0;

    requestAnimationFrame((timestamp) => this.#tick(timestamp));
  }

  check(midiEvent, simultaneousEvents) {
    if (!midiEvent.isKeyPressEvent()) {
      return false;
    }

    this.#lastCheckTimestamp = performance.now();

    if (!this.#isStarted()) {
      this.#start();
    }

    this.#testedMidiEvents.addEvent(midiEvent);

    if (this.#testedMidiEvents.isEqualByKeypressEvents(simultaneousEvents)) {
      this.#reset();

      return true;
    }

    if (this.#testedMidiEvents.getSize(true) >= simultaneousEvents.getSize(true)) {
      this.#reset();
    }

    return false;
  }

  #start() {
    requestAnimationFrame((timestamp) => this.#tick(timestamp));
  }

  // eslint-disable-next-line no-dupe-class-members
  #isStarted() {
    return this.#testedMidiEvents.getSize() > 0;
  }

  // eslint-disable-next-line no-dupe-class-members
  #reset() {
    this.#testedMidiEvents = new SimultaneousMidiEvents([]);
  }

  // eslint-disable-next-line no-dupe-class-members
  #tick(now) {
    const delta = now - this.#lastCheckTimestamp;

    if (delta >= this.#threshold) {
      this.#reset();

      return;
    }

    requestAnimationFrame((timestamp) => this.#tick(timestamp));
  }
}
