<template>
  <div ref="staves" class="staves page__staves">
    <div :key="stavesElementKey" ref="stavesTarget" class="staves-target">
      <slice-controls
        v-if="isSelectingSlice"
        class="staves-target__slice-controls"
        :left="leftHandleXCoord"
        :right="rightHandleXCoord"
        @update-right="onUpdateRight"
        @update-left="onUpdateLeft"
      />
    </div>
  </div>
</template>

<script>
import * as opensheetmusicdisplay from 'opensheetmusicdisplay';
import { StavesService } from '@/services/staves/StavesService';
import { SeparateNoteService } from '@/services/staves/SeparateNoteService';
import { songControlsStore, setSongControlsStoreValue } from '@/store/songControls';
import SliceControls from '@/components/SliceControls';

export default {
  name: 'Staves',
  components: { SliceControls },
  props: {
    selectedSong: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      cursorStep: 35,
      stavesElementKey: 0,
      stavesService: null,
      osmd: null,

      xStepsCoords: [],
      leftHandleXCoord: 0,
      rightHandleXCoord: 0,
    };
  },
  computed: {
    isSelectingSlice() {
      return songControlsStore.isSelectingSlice;
    },
    tracks() {
      return songControlsStore.tracks;
    },
    from() {
      return songControlsStore.from;
    },
    to() {
      return songControlsStore.to;
    },
  },
  async mounted() {
    await this.init({ from: 0, to: null, tracks: 'both' });
    this.fillXStepsCoords();
    await this.restart();

    this.leftHandleXCoord = this.getXCoordByStep(this.from);
    this.rightHandleXCoord =
      this.to !== null ? this.getXCoordByStep(this.to - 1) : this.getXCoordByStep(this.from + 5);
  },
  methods: {
    onUpdateRight(x) {
      this.rightHandleXCoord = x;
      const value = this.getStepByXCoord(x) + 1; // make range inclusive
      setSongControlsStoreValue('to', value);
    },
    onUpdateLeft(x) {
      this.leftHandleXCoord = x;
      setSongControlsStoreValue('from', this.getStepByXCoord(x));
    },
    createOsmd() {
      const osmd = new opensheetmusicdisplay.OpenSheetMusicDisplay(this.$refs.stavesTarget);
      osmd.setOptions({
        backend: 'svg',
        drawTitle: false,
        drawMeasureNumbers: true,
        drawPartNames: false,
        renderSingleHorizontalStaffline: true,
        cursorsOptions: [
          {
            type: 0,
            color: '#ff0000',
            alpha: 0.5,
          },
        ],
      });

      return osmd;
    },
    async init({ tracks, from, to }) {
      const { xmlUrl } = this.selectedSong;
      const osmd = this.createOsmd();
      this.osmd = osmd;

      const separateNoteService = new SeparateNoteService();
      const stavesService = new StavesService({
        xmlUrl,
        osmd,
        from,
        to,
        tracks,
        separateNoteService,
      });
      this.stavesService = stavesService;
      await stavesService.start();

      setSongControlsStoreValue('currentStep', this.stavesService.getCurrentStep());
    },
    fillXStepsCoords() {
      while (!this.osmd.cursor.iterator.endReached) {
        const { width, x } = this.osmd.cursor.cursorElement.getBoundingClientRect();
        this.xStepsCoords.push(x + width / 2);
        this.stavesService.next();
      }
    },
    getStepByXCoord(x) {
      return this.xStepsCoords
        .map((stepX, i) => {
          return { step: i, diff: Math.abs(stepX - x) };
        })
        .sort((a, b) => b.diff - a.diff)
        .pop().step;
    },
    getXCoordByStep(step) {
      return this.xStepsCoords[step];
    },
    play() {
      this.stavesService.next();
      this.osmd.cursor.cursorElement.scrollIntoView({ inline: 'center' });
      setSongControlsStoreValue('currentStep', this.stavesService.getCurrentStep());
    },
    async restart() {
      const { tracks, from, to } = this;
      this.stavesElementKey += 1;
      await this.$nextTick();
      await this.init({ tracks, from, to });
    },
  },
};
</script>

<style scoped>
.staves {
  width: 100%;
  overflow-x: scroll;
  position: relative;
}

.staves__slice-btn {
  position: fixed;
  right: 100px;
  bottom: 100px;
}

.staves-target {
  position: relative;
}

.staves-target__slice-controls {
  position: absolute;
  height: 100%;
  z-index: 10;
}
</style>
