<template>
  <div class="octave-container">
    <div
      :class="{
        octave: true,
      }"
    >
      <div
        v-for="n of keys"
        :key="n"
        :class="{
          'octave-key': true,
          octave__key: true,
          'octave__key--black': getKeyColor(n) === 'b',
          'octave__key--on': isKeyPressed(n),
          'octave__key--should-be-pressed': shouldKeyBePressed(n),
        }"
      >
        <span v-show="shouldKeyBePressed(n)" class="octave-key__note-name">{{
          getNoteName(n)
        }}</span>
      </div>
    </div>
    <span class="octave-container__name" :style="{ '--bg-color': octaveConfig.color }">{{
      octaveConfig.name
    }}</span>
  </div>
</template>

<script>
const Octave = (name, color) => ({ name, color });

const OCTAVE_CONFIG = {
  1: Octave('субконтр', 'red'),
  2: Octave('контр', 'orange'),
  3: Octave('большая', 'yellow'),
  4: Octave('малая', 'yellowgreen'),
  5: Octave('первая', 'green'),
  6: Octave('вторая', 'cyan'),
  7: Octave('третья', 'lightblue'),
  8: Octave('четвертая', 'blue'),
  9: Octave('пятая', 'red'),
};

const NOTE_NAME = {
  0: 'До',
  1: '#До',
  2: 'Ре',
  3: '#Ре',
  4: 'Ми',
  5: 'Фа',
  6: '#Фа',
  7: 'Соль',
  8: '#Соль',
  9: 'Ля',
  10: '#Ля',
  11: 'Си',
};

export default {
  name: 'PianoOctave',
  props: {
    number: {
      type: Number,
      required: true,
    },
    pressedKeys: {
      type: Set,
      default: () => new Set(),
    },
    shouldBePressedKeys: {
      type: Set,
      default: () => new Set(),
    },
  },
  computed: {
    keys() {
      return Array(12)
        .fill(0)
        .map((_, i) => i)
        .slice(...this.range);
    },
    range() {
      if (this.number === 1) {
        return [9, 12];
      }

      if (this.number === 9) {
        return [0, 1];
      }

      return [0, 12];
    },
    octaveConfig() {
      return OCTAVE_CONFIG[this.number];
    },
  },
  methods: {
    getKeyColor(n) {
      if (this.number === 1) {
        return n % 2 !== 0 ? 'w' : 'b';
      }

      if (n < 5) {
        return n % 2 === 0 ? 'w' : 'b';
      }

      if (n === 5) {
        return 'w';
      }

      return n % 2 !== 0 ? 'w' : 'b';
    },
    getNoteName(n) {
      return NOTE_NAME[n];
    },
    isKeyPressed(n) {
      const noteNumber = this.number * 12 + n;

      return this.pressedKeys.has(noteNumber);
    },
    shouldKeyBePressed(n) {
      const noteNumber = this.number * 12 + n;

      return this.shouldBePressedKeys.has(noteNumber);
    },
  },
};
</script>

