  box-sizing: border-box;
}

.octave__key--black {
  background-color: black;
  height: 200px;
  width: 30px;
  position: absolute;
  top: 0;
  z-index: 100;
}

.octave__key--should-be-pressed {
  background-color: blue;
}

.octave__key--on {
  background-color: orange;
}

.octave__key--black:nth-child(2) {
  left: 35px;
}

.octave__key--black:nth-child(4) {
