import { BaseMidiEventEmitter } from '@/services/BaseMidiEventEmitter';
import { getSimultaneousEvents } from '@/services/MidiFileAdapter';

const getEventsSlice = (start, end, events) => {
  // slice by voiceEntryIndex
  return events.reduce(
    (acc, event) => {
      const index = event.getVoiceEntryIndex();

      if (index !== null && index >= start && index < end) {
        acc.events.push(event);
        acc.doesIntersectWithSlice = true;

        return acc;
      }

      if (index !== null && index >= end) {
        acc.doesIntersectWithSlice = false;

        return acc;
      }

      if (index === null && acc.doesIntersectWithSlice) {
        acc.events.push(event);
      }

      return acc;
    },
    { events: [], doesIntersectWithSlice: false }
  ).events;
};

export class MidiFileEventEmitterFreeTempo extends BaseMidiEventEmitter {
  #fileData;
  #tracks;
  #fullListOfEvents;
  #events;
  #lastEvent;
  #from;
  #to;

  constructor(fileData, options) {
    super();

    this.#fileData = fileData;

    this.#init(options);
  }

  #init({ from, to, tracks }) {
    this.#tracks = tracks;
    this.#fullListOfEvents = getSimultaneousEvents(this.#fileData, { tracks: this.#tracks });
    this.#from = from;
    this.#to = to || this.#fullListOfEvents.length;

    this.#events = getEventsSlice(this.#from, this.#to, this.#fullListOfEvents);
    this.#events.reverse();

    this.#lastEvent = null;
  }

  play() {
    const event = this.#events.pop();
    this.#lastEvent = event;

    if (event) {
      this.dispatchEvent(event);
    }

    if (!event) {
      this.dispatchEvent('end');

      return;
    }

    if (event && !event.hasKeyPressEvents()) {
      this.play();
    }
  }

  restart({ from, to, tracks }) {
    this.#init({ from, to, tracks });
  }

  getLastEvent() {
    return this.#lastEvent;
  }
}
