export class MidiEvent {
  constructor(eventType, note, deltaTime, trackIndex = null) {
    this.eventType = eventType;
    this.note = note;
    this.deltaTime = deltaTime;
    this.trackIndex = trackIndex;
  }

  withDeltaTime(deltaTime) {
    return new MidiEvent(this.eventType, this.note, deltaTime, this.trackIndex);
  }

  getTrackIndex() {
    return this.trackIndex;
  }

  getNote() {
    return this.note;
  }

  getEventType() {
    return this.eventType;
  }

  isKeyPressEvent() {
    return this.eventType === 'on';
  }

  isKeyReleaseEvent() {
    return this.eventType === 'off';
  }

  getDeltaTime() {
    return this.deltaTime;
  }

  isSameNote(midiEvent) {
    return this.note === midiEvent.note;
  }

  isEqualEvent(midiEvent) {
    return this.isSameNote(midiEvent) && this.eventType === midiEvent.eventType;
  }
}
