export class BaseMidiEventEmitter {
  constructor() {
    this.callbacks = {
      on: [],
      off: [],
      end: [],
      pause: [],
      simultaneous: [],
    };
  }

  addListener(eventType, cb) {
    this.callbacks[eventType].push(cb);
  }

  removeListener(eventType, cb) {
    this.callbacks[eventType] = this.callbacks[eventType].filter((listener) => listener !== cb);
  }

  dispatchEvent(event) {
    const eventType = event.eventType || event;
    this.callbacks[eventType].forEach((cb) => cb(event));
  }

  removeAllListeners() {
    Object.keys(this.callbacks).forEach((event) => {
      this.callbacks[event] = [];
    });
  }
}
