<template>
  <div class="keyboard-wrap">
    <div class="keyboard-wrap__keyboard keyboard">
      <piano-octave
        v-for="octN of octaves"
        :key="octN"
        class="keyboard__octave"
        :pressed-keys="pressedKeys"
        :should-be-pressed-keys="shouldBePressedKeys"
        :number="octN"
      />
    </div>

    <button v-if="isDebug" class="keyboard-wrap__next" @click="continuePlayingFile">next</button>
  </div>
</template>

<script>
import PianoOctave from '@/components/PianoOctave.vue';
import { midiKeyboardEventEmitter } from '@/services/MidiKeyboardEventEmitter';
import { SimultaneousMidiEventsComparator } from '@/services/SimultaneousMidiEventsComparator';
import { getOctaveRange } from '@/services/MidiFileAdapter';
import { MidiFileEventEmitterFreeTempo } from '@/services/MidiFileEventEmitterFreeTempo';
import { songControlsStore } from '@/store/songControls';
import { dispatchRestart } from '@/store/restartAction';

export default {
  name: 'PianoKeyboard',
  components: { PianoOctave },
  props: {
    songUrl: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      pressedKeys: null,
      shouldBePressedKeys: null,
      midiFileEventEmitter: null,
      eventsComparator: null,
      song: null,
    };
  },
  watch: {
    songUrl: 'loadSong',
    song: 'onSongLoaded',
  },
  async mounted() {
    await this.loadSong();
  },
  computed: {
    from() {
      return songControlsStore.from;
    },
    to() {
      return songControlsStore.to;
    },
    tracks() {
      return songControlsStore.tracks;
    },
    octaves() {
      if (!this.song) {
        return [];
      }

      const range = getOctaveRange(this.song);

      return Array(9)
        .fill(0)
        .map((_, i) => i + 1)
        .slice(...range);
    },
    isDebug() {
      return this.$route.query?.debug;
    },
  },
  methods: {
    restart() {
      this.midiFileEventEmitter.restart({ from: this.from, to: this.to, tracks: this.tracks });
      this.midiFileEventEmitter.play();
    },
    async onSongLoaded() {
      this.pressedKeys = new Set();
      this.shouldBePressedKeys = new Set();
      this.midiFileEventEmitter = new MidiFileEventEmitterFreeTempo(this.song, {
        from: this.from,
        to: this.to,
        tracks: this.tracks,
      });
      this.eventsComparator = new SimultaneousMidiEventsComparator(100);

      this.midiFileEventEmitter.addListener('simultaneous', this.onMidiFileEvent);
      this.midiFileEventEmitter.addListener('end', () => dispatchRestart());
      this.midiFileEventEmitter.play();

      midiKeyboardEventEmitter.addListener('on', this.onMidiKeyboardEvent);
      midiKeyboardEventEmitter.addListener('off', this.onMidiKeyboardEvent);
      await midiKeyboardEventEmitter.start();
    },
    async loadSong() {
      const response = await fetch(this.songUrl);

      this.song = await response.json();
    },
    continuePlayingFile() {
      this.midiFileEventEmitter.play();
      this.$emit('continue-playing');
    },
    onMidiKeyboardEvent(e) {
      if (e.isKeyPressEvent()) {
        this.pressedKeys.add(e.getNote());
      } else {
        this.pressedKeys.delete(e.getNote());
      }

      this.pressedKeys = new Set(this.pressedKeys);

      if (this.eventsComparator.check(e, this.midiFileEventEmitter.getLastEvent())) {
        this.continuePlayingFile();
      }
    },
    onMidiFileEvent(simultaneousEvents) {
      const shouldBePressedKeys = new Set();

      simultaneousEvents.getMidiEvents().forEach((midiEvent) => {
        if (midiEvent.isKeyPressEvent()) {
          shouldBePressedKeys.add(midiEvent.getNote());
        } else {
          shouldBePressedKeys.delete(midiEvent.getNote());
        }
      });

      this.shouldBePressedKeys = shouldBePressedKeys;
    },
  },
};
</script>

<style scoped>
.keyboard {
  display: flex;
  flex-wrap: nowrap;
}

.keyboard__octave {
  flex: 0 0 auto;
}

.keyboard-wrap__next {
  font-size: 28px;
  position: fixed;
  bottom: 80px;
  left: 50%;
  transform: translateX(-50%);
}

.keyboard-wrap__keyboard {
  width: fit-content;
  margin: 0 auto;
}
</style>
