<template>
  <div class="page">
    <div class="keyboard page__keyboard">
      <piano-keyboard
        ref="keyboard"
        v-if="selectedSong"
        :key="songId"
        :song-url="selectedSong.midJsonUrl"
        @continue-playing="onContinue"
      />
    </div>

    <staves
      ref="staves"
      v-if="selectedSong"
      :key="songId"
      class="page__staves"
      :selected-song="selectedSong"
    />

    <song-controls />
  </div>
</template>

<script>
import PianoKeyboard from '@/components/PianoKeyboard';
import Staves from '@/components/Staves';
import SongControls from '@/components/SongControls';
import { addRestartListener } from '@/store/restartAction';

export default {
  name: 'Song',
  components: { PianoKeyboard, Staves, SongControls },
  props: {
    songId: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      songOptions: [],
    };
  },
  async mounted() {
    addRestartListener(this.restart);
    await this.loadSongOptions();
  },
  computed: {
    selectedSong() {
      return this.songOptions.find((option) => option.id === this.songId);
    },
  },
  methods: {
    async loadSongOptions() {
      const response = await fetch(`${process.env.VUE_APP_API_BASE_URL}/content/songs.json`);
      this.songOptions = await response.json();
    },
    onContinue() {
      this.$refs.staves.play();
    },
    restart() {
      this.$refs.keyboard.restart();
      this.$refs.staves.restart();
    },
  },
};
</script>

<style scoped>
.page {
  height: 100vh;
}

.page__staves {
  margin-top: auto;
}

.keyboard {
  width: 100%;
  overflow-x: auto;
}

.restart-btn {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}
</style>
