import { MidiEvent } from '@/models/MidiEvent';
import { SimultaneousMidiEvents } from '@/models/SimultaneousMidiEvents';
import lodash from 'lodash';

export const DeltaTimeCalculator = (ticksPerBeat, trackTempo) => (deltaTicks) =>
  (trackTempo * 0.001 * deltaTicks) / ticksPerBeat;

export const getNotesEvents = (parsedMidiFileData, trackIndex) => {
  const {
    tracks,
    header: { ticksPerBeat },
  } = parsedMidiFileData;

  const flattenEvents = tracks.flat();
  const { microsecondsPerBeat } = flattenEvents.find((e) => e.subtype === 'setTempo');
  const deltaTimeCalculator = DeltaTimeCalculator(ticksPerBeat, microsecondsPerBeat);
  const chosenTrackEvents = tracks[trackIndex] || [];

  return chosenTrackEvents
    .flat()
    .filter((event) => ['noteOn', 'noteOff'].includes(event.subtype))
    .map((event) => {
      const eventType = event.subtype === 'noteOn' ? 'on' : 'off';

      return new MidiEvent(
        eventType,
        event.noteNumber,
        deltaTimeCalculator(event.deltaTime),
        trackIndex
      );
    });
};

export const joinTracks = (midiEventsTrackA, midiEventsTrackB) => {
  const trackAReversed = [...midiEventsTrackA].reverse();
  const trackBReversed = [...midiEventsTrackB].reverse();
  let lastTrackUsed = midiEventsTrackA;
  let lastEventsTotalDelta = 0;
  const jointEvents = [];

  const getMinDelta = (a, b) => {
    const aOffset = lastTrackUsed === trackAReversed ? 0 : lastEventsTotalDelta;
    const bOffset = lastTrackUsed === trackBReversed ? 0 : lastEventsTotalDelta;

    const deltaA = a ? a.getDeltaTime() - aOffset : Infinity;
    const deltaB = b ? b.getDeltaTime() - bOffset : Infinity;
    const minDelta = lodash.min([deltaA, deltaB]);
    const nearestEvent = minDelta === deltaA ? a : b;

    return { minDelta, nearestEvent };
  };

  let a = trackAReversed.pop();
  let b = trackBReversed.pop();
  while (a || b) {
    const { minDelta, nearestEvent } = getMinDelta(a, b);

    let trackUsed;
    if (nearestEvent === a) {
      trackUsed = trackAReversed;
      jointEvents.push(a.withDeltaTime(minDelta));
      a = trackAReversed.pop();
    } else {
      trackUsed = trackBReversed;
      jointEvents.push(b.withDeltaTime(minDelta));
      b = trackBReversed.pop();
    }

    if (trackUsed !== lastTrackUsed) {
      lastEventsTotalDelta = 0;
    }

    lastEventsTotalDelta += minDelta;
    lastTrackUsed = trackUsed;
  }

  return jointEvents;
};

export const getSimultaneousEvents = (parsedMidiFileData, options) => {
  const { tracks } = options; // 'both' | 'first' | 'second'
  const notesGetter = () => {
    const notesA = getNotesEvents(parsedMidiFileData, 0);
    const notesB = getNotesEvents(parsedMidiFileData, 1);

    return joinTracks(notesA, notesB);
  };

  const notes = notesGetter();

  const SHORTEST_NOTE_LENGTH = 20; // ms

  const bothTrackEvents = notes
    .reduce((simultaneousEvents, note) => {
      // put midi events which happened close to each other in time in one SimultaneousMidiEvents object
      const lastSimultaneousEvent = simultaneousEvents.pop();

      if (!lastSimultaneousEvent) {
        return [new SimultaneousMidiEvents([note])];
      }

      if (note.getDeltaTime() < SHORTEST_NOTE_LENGTH) {
        lastSimultaneousEvent.addEvent(note);

        return [...simultaneousEvents, lastSimultaneousEvent];
      }

      return [
        ...simultaneousEvents,
        lastSimultaneousEvent,
        new SimultaneousMidiEvents([note], lastSimultaneousEvent.getCumulativeDeltaTimeShift()),
      ];
    }, [])
    .reduce(
      (acc, simultaneousEvent) => {
        // add voice entry index to each SimultaneousMidiEvents object which contains key press events
        if (simultaneousEvent.hasKeyPressEvents()) {
          simultaneousEvent.setVoiceEntryIndex(acc.currentVoiceEntryIndex);
          acc.currentVoiceEntryIndex += 1;
        }

        acc.events.push(simultaneousEvent);
        return acc;
      },
      { events: [], currentVoiceEntryIndex: 0 }
    ).events;

  if (tracks === 'first' || tracks === 'second') {
    const trackIndex = tracks === 'first' ? 0 : 1;

    bothTrackEvents.forEach((event) => {
      event.filterEventsByTrackIndex(trackIndex);
    });
  }

  return bothTrackEvents.filter((event) => !event.isEmpty());
};

export const getOctaveRange = (song) => {
  const firstTrackEvents = getNotesEvents(song, 0);
  const secondTrackEvents = getNotesEvents(song, 1);
  const notes = [...firstTrackEvents, ...secondTrackEvents].map((e) => e.getNote());
  const min = lodash.min(notes);
  const max = lodash.max(notes);

  return [Math.floor(min / 12) - 1, Math.floor(max / 12)];
};
