import { MidiEvent } from '@/models/MidiEvent';
import { BaseMidiEventEmitter } from '@/services/BaseMidiEventEmitter';

class MidiKeyboardEventEmitter extends BaseMidiEventEmitter {
  constructor() {
    super();

    this.isStarted = false;

    this.emittionTime = 0;
  }

  async start() {
    if (this.isStarted) {
      return;
    }

    this.isStarted = true;

    if (!navigator.requestMIDIAccess) {
      alert('Browser does not support MIDI');

      return;
    }

    const midi = await navigator.requestMIDIAccess();

    for (const input of midi.inputs.values()) {
      input.onmidimessage = (message) => {
        const [typeNumber, note] = message.data;
        let eventType;

        if (typeNumber === 144) {
          eventType = 'on';
        } else if (typeNumber === 128) {
          eventType = 'off';
        } else {
          return;
        }

        const now = performance.now();
        const deltaTime = now - this.emittionTime;
        this.emittionTime = now;

        this.dispatchEvent(new MidiEvent(eventType, note, deltaTime));
      };
    }
  }
}

export const midiKeyboardEventEmitter = new MidiKeyboardEventEmitter();
