export class SeparateNoteService {
  #tieLists;
  #isSeparateNoteCache;

  constructor() {
    // keys mean track index
    this.#tieLists = {
      0: [],
      1: [],
    };
    this.#isSeparateNoteCache = {
      0: {},
      1: {},
    };
  }

  /**
   * Checks if the entry is one of those
   * that need to be played as a separate note
   * (e.g. rest is not one of those, the note
   * that connected with previous note by tie
   * is also not one of those; everything else
   * is need to be played).
   */
  isSeparateNote(voiceEntry, voiceEntryIndex, trackIndex) {
    const isSeparateNoteCache = this.#isSeparateNoteCache[trackIndex];

    const cachedResult = isSeparateNoteCache[voiceEntryIndex];
    if (typeof cachedResult === 'boolean') {
      return cachedResult;
    }

    const notes = voiceEntry?.notes || [];

    const hasNotes = notes.some((note) => typeof note.pitch?.halfTone === 'number');

    const ties = notes
      .map((note) => note.tie?.notes?.[0])
      .filter((tieNote) => typeof tieNote?.halfTone === 'number');
    const hasTies = ties.length > 0;

    const pitchCountDict = this.#tieLists[trackIndex].reduce((acc, pitch) => {
      if (!acc[pitch]) {
        acc[pitch] = 0;
      }

      acc[pitch] += 1;

      return acc;
    }, {});
    this.#tieLists[trackIndex] = [...this.#tieLists[trackIndex], ...ties];
    const hasOpeningTies = ties.some((pitch) => {
      const count = pitchCountDict[pitch] || 0;
      return count % 2 === 0;
    });

    const result = (hasNotes && !hasTies) || hasOpeningTies;
    isSeparateNoteCache[voiceEntryIndex] = result;

    return result;
  }
}
